export default class Node {
  constructor(position) {
    this.position = position;
    this.edges = [];
    this.parent = null;
    this.visited = false;
  }

  connect() {
    for (let i = 0; i < arguments.length; i += 1) {
      // eslint-disable-next-line prefer-rest-params
      if (!this.findEdge(arguments[i])) {
        // eslint-disable-next-line prefer-rest-params
        this.edges.push(arguments[i]);
      }
    }
  }

  findEdge(searchEdge) {
    return this.edges.find((edge) => edge.position.x === searchEdge.position.x
      && edge.position.y === searchEdge.position.y);
  }
}
