const bfs = (graph) => {
  // Create a queue ad path
  const queue = [];
  const path = [];
  // Get started
  queue.push(graph.start);
  while (queue.length > 0) {
    const person = queue.shift();
    // If not already checked
    if (!person.visited) {
      // If we're done!
      if (person.position.x === graph.end.position.x
      && person.position.y === graph.end.position.y) {
        // Figure out the path by going backwards through parent nodes
        path.push(person);
        let next = person.parent;
        while (next) {
          path.push(next);
          next = next.parent;
        }
        break;
      } else {
        // Look at node's neighbors
        const next = person.edges;
        for (let i = 0; i < next.length; i += 1) {
          // Place them all in the queue and update parent
          const neighbor = next[i];
          if (!neighbor.visited) {
            queue.push(neighbor);
            neighbor.parent = person;
          }
        }
        // Mark that node as visited
        person.visited = true;
      }
    }
  }

  return path;
};
module.exports = bfs;
