<template>
    <div class="p-5">
        <h3>Click to draw the maze and click to white path to select pick point!</h3>
        <div class="d-flex flex-row align-items-center mb-3">
            <label class="mr-3">Speed</label>
            <div class="form-group pt-2 mr-3">
                <input type="range" v-model="speedRate" min="0" max="100" class="form-control-range"
                       :disabled="playing">
            </div>
            <label class="mr-3">Mode</label>
            <div class="form-group pt-2 mr-3">
                <select class="form-control" v-model="mode" :disabled="playing">
                    <option v-for="(option, index) in modeOptions"
                            :value="option.value" :key="index">{{ option.label }}
                    </option>
                </select>
            </div>
            <div style="margin-top: -8px" class="mr-3">
                <button type="button" class="btn btn-primary"
                        @click="drawGame" :disabled="playing">Draw
                </button>
            </div>
            <div style="margin-top: -8px" class="mr-3">
                <button type="button" class="btn btn-primary"
                        @click="startGame" :disabled="playing || !numOfRunCell">{{playing ? 'Playing...' : 'Start'}}
                </button>
            </div>
            <div style="margin-top: -8px">
                <button type="button" class="btn btn-primary"
                        @click="resetKeepMaze" :disabled="playing">Clear
                </button>
            </div>
        </div>
        <div class="maze d-flex flex-row">
            <div class="maze-row" v-for="(row, rowIndex) in maze" :key="rowIndex">
                <div class="cell" :class="getActiveClass(cell, rowIndex, colIndex)"
                     v-for="(cell, colIndex) in row" :key="colIndex"
                     @click.end="selectPick({ x: rowIndex, y: colIndex })"
                ></div>
            </div>
        </div>
    </div>
</template>

<script>
    import Graph from '../helpers/graph';
    import bfs from '../helpers/bfs';
    import { createArray, nextFrame, getNeighbours } from '../helpers/maze';

    const width = 29;
    const height = 29;
    let stack = [];
    let start = {x: 1, y: 1};

    export default {
        name: 'Maze',
        data() {
            return {
                maze: [],
                wasHere: [],
                start: {
                    x: 1, y: 1,
                },
                cursor: {
                    x: 1, y: 1,
                },
                target: {
                    x: 27, y: 27,
                },
                picks: [],
                stack: [],
                round: 0,
                numOfRunCell: 0,
                stackPath: [],
                shortestPath: [],
                modeOptions: [
                    {label: 'First meet', value: 'reach'},
                    {label: 'Shortest path', value: 'shortest'},
                ],
                mode: 'reach',
                graph: new Graph(),
                speedRate: 50,
                playing: false,
                meetTarget: false,
            };
        },
        computed: {
            speedLevel() {
                return 10 + (101 - +this.speedRate);
            },
        },
        mounted() {
          this.drawGame();
        },
      methods: {
            selectPick(point) {
                if (this.picks.length >= 5 || this.maze[point.x][point.y] === 1) return;
                const findPoint = this.picks.find((p) => p.x === point.x && p.y === point.y);
                if (!findPoint) {
                    this.picks.push({...point, meet: false, picked: false});
                }
            },
            async resetKeepMaze() {
                const init = createArray(width, height, 0);
                this.wasHere = [...init];
                this.stack = [];
                this.stackPath = [];
                this.shortestPath = [];
                this.cursor = this.start;
                this.graph = new Graph();
                this.meetTarget = false;
                this.picks = [];
            },
            async resetGame() {
                this.maze = createArray(width, height);
                await this.resetKeepMaze();
            },
            randomPick() {
                this.picks = [];
                for (let i = 0; i < 3; i += 1) {
                    for (let j = this.maze[i].length - 3; j < this.maze[i].length; j += 1) {
                        if (this.maze[i][j] === 0) {
                            this.picks.push({
                                x: i,
                                y: j,
                                meet: false,
                                picked: false,
                            });
                            break;
                        }
                        if (this.picks.length) break;
                    }
                }
                for (let i = this.maze.length - 3; i < this.maze.length; i += 1) {
                    for (let j = 0; j < 3; j += 1) {
                        if (this.maze[i][j] === 0) {
                            this.picks.push({
                                x: i,
                                y: j,
                                meet: false,
                                picked: false,
                            });
                            break;
                        }
                    }
                    if (this.picks.length > 1) break;
                }
            },
            async drawGame() {
                await this.resetGame();
                await this.createMaze();
                this.numOfRunCell = this.maze.reduce((sum, b) => sum + b.filter((i) => i === 0).length, 0);
            },
            async startGame() {
                this.playing = true;
                await this.solveMaze();
                this.playing = false;
            },
            getActiveClass(cell, rowIndex, colIndex) {
                if (cell === 1) return 'wall';
                const pick = this.picks.find((item) => item.x === rowIndex && item.y === colIndex);
                if (pick) {
                    if (pick.picked) return 'picked';
                    return 'pick';
                }
                if (this.cursor.x === rowIndex && this.cursor.y === colIndex) {
                    if (this.picked) return 'pick';
                    return 'cursor';
                }
                if (this.target.x === rowIndex && this.target.y === colIndex) return 'target';
                return this.wasHere[rowIndex][colIndex] ? 'visited' : 'unvisited';
            },
            async sleep(n) {
                await new Promise((r) => setTimeout(r, n));
            },
            async recursiveSolve(x, y, back = false) {
                this.round += 1;
                const wall = 1;
                const node = this.graph.findNodeOrCrete({x, y});
                if (x !== 0 && this.maze[x - 1][y] !== wall) {
                    node.connect(this.graph.findNodeOrCrete({
                        x: x - 1,
                        y,
                    }));
                }
                if (x !== width - 1 && this.maze[x + 1][y] !== wall) {
                    node.connect(this.graph.findNodeOrCrete({
                        x: x + 1,
                        y,
                    }));
                }
                if (y !== 0 && this.maze[x][y - 1] !== wall) {
                    node.connect(this.graph.findNodeOrCrete({
                        x,
                        y: y - 1,
                    }));
                }
                if (y !== height - 1 && this.maze[x][y + 1] !== wall) {
                    node.connect(this.graph.findNodeOrCrete({
                        x,
                        y: y + 1,
                    }));
                }
                await this.sleep(this.speedLevel);
                this.$nextTick(() => {
                    this.$set(this, 'cursor', {x, y});
                });
                if (!back) {
                    this.stack.push({x, y});
                }
                const numOfVisited = this.wasHere.reduce((sum, b) => sum + b.filter((i) => i === 1).length, 0);
                if (this.numOfRunCell === numOfVisited) {
                    return this.stackPath.length >= 1;
                }
                const pick = this.picks.find((item) => item.x === x && item.y === y);
                if (pick) {
                    pick.meet = true;
                    const meetAllPick = this.picks.filter((item) => item.meet).length === this.picks.length;
                    if (this.meetTarget && meetAllPick) {
                        if (this.mode === 'reach') {
                            this.stackPath.push(JSON.parse(JSON.stringify(this.stack)));
                            return true;
                        }
                        this.stackPath.push(JSON.parse(JSON.stringify(this.stack)));
                    }
                }
                if (x === this.target.x && y === this.target.y) {
                    this.meetTarget = true;
                    const meetAllPick = this.picks.filter((item) => item.meet).length === this.picks.length;
                    if (this.mode === 'reach' && meetAllPick) {
                        this.stackPath.push(JSON.parse(JSON.stringify(this.stack)));
                        return true;
                    }
                    if (this.numOfRunCell > numOfVisited && meetAllPick) {
                        this.stackPath.push(JSON.parse(JSON.stringify(this.stack)));
                        if (this.stackPath.length > 1) return true;
                        const backPoint = this.stack.pop();
                        await this.recursiveSolve(backPoint.x, backPoint.y, true);
                    } else if (meetAllPick) {
                        return true;
                    }
                }
                this.$nextTick(() => {
                    this.$set(this.wasHere[x], `${y}`, 1);
                });

                if (x !== 0 && this.maze[x - 1][y] !== wall && this.wasHere[x - 1][y] !== 1) {
                    if (back) this.stack.push({x, y});
                    if (await this.recursiveSolve(x - 1, y)) {
                        return true;
                    }
                } else if (x !== width - 1 && this.maze[x + 1][y] !== wall && this.wasHere[x + 1][y] !== 1) {
                    if (back) this.stack.push({x, y});
                    if (await this.recursiveSolve(x + 1, y)) {
                        return true;
                    }
                } else if (y !== 0 && this.maze[x][y - 1] !== wall && this.wasHere[x][y - 1] !== 1) {
                    if (back) this.stack.push({x, y});
                    if (await this.recursiveSolve(x, y - 1)) {
                        return true;
                    }
                } else if (y !== height - 1 && this.maze[x][y + 1] !== wall && this.wasHere[x][y + 1] !== 1) {
                    if (back) this.stack.push({x, y});
                    if (await this.recursiveSolve(x, y + 1)) {
                        return true;
                    }
                } else if (this.stack.length && this.stack.length < width * height) {
                    const backPoint = this.stack.pop();
                    await this.recursiveSolve(backPoint.x, backPoint.y, true);
                }
                return this.stackPath.length >= 1;
            },
            async solveMaze() {
                const result = await this.recursiveSolve(this.cursor.x, this.cursor.y);
                console.log('result', result);
                if (result) {
                    this.graph.start = this.graph.findNode(this.start);
                    this.graph.end = this.graph.findNode(this.target);
                    this.graph.setPicks(this.picks.map((item) => (this.graph.findNode({
                        x: item.x,
                        y: item.y,
                    }))));
                    const scenarios = this.graph.getScenarios();
                    const routes = scenarios.map((scenario) => {
                        let path = [];
                        for (let i = 0; i < scenario.length - 1; i += 1) {
                            this.graph.start = scenario[i];
                            this.graph.end = scenario[i + 1];
                            const pickPath = bfs(this.graph);
                            this.graph.nodes.map((n) => {
                                // eslint-disable-next-line
                                n.visited = false;
                                // eslint-disable-next-line
                                n.parent = null;
                                return false;
                            });
                            path = path.concat(pickPath.reverse());
                        }
                        return path;
                    });
                    let shortestPath;
                    routes.map((route) => {
                        if (!shortestPath) shortestPath = route;
                        if (route.length < shortestPath.length) shortestPath = route;
                        return false;
                    });
                    this.wasHere = createArray(width, height, 0);

                    this.shortestPath = shortestPath.map((i) => i.position);
                    await this.runShortestPath();
                }
            },
            async createMaze() {
                const neighbours = getNeighbours(start.x, start.y, 1, this.maze, width, height);
                let l;
                if (neighbours.length < 1) {
                    if (stack.length < 1) {
                        stack = [];
                        start.x = 1;
                        start.y = 1;
                        return;
                    }
                    start = stack.pop();
                } else {
                    const i = 2 * Math.floor(Math.random() * (neighbours.length / 2));
                    l = neighbours[i];
                    this.$set(this.maze[l.x], `${l.y}`, 0);
                    l = neighbours[i + 1];
                    this.$set(this.maze[l.x], `${l.y}`, 0);
                    start = l;
                    stack.push(start);
                }
                await nextFrame();
                await this.createMaze();
            },
            async runShortestPath() {
                if (!this.shortestPath.length) {
                    this.$set(this, 'cursor', this.target);
                    return;
                }
                const p = this.shortestPath.shift();
                const pickPoint = this.picks.find((item) => item.x === p.x && item.y === p.y);
                if (pickPoint) {
                    pickPoint.picked = true;
                }
                this.$set(this, 'cursor', p);
                this.wasHere[p.x][p.y] = 1;
                await this.sleep(this.speedLevel);
                await this.runShortestPath();
            },
        },
    };
</script>

<style scoped lang="scss">
    .maze {
    }

    .maze-row {
        width: 20px;
        display: flex;
        flex-direction: column;
    }

    .cell {
        width: 20px;
        height: 20px;
        display: inline-block;
    }

    .wall {
        background-color: plum;
    }

    .bg-green {
        background-color: green;
    }

    .cursor {
        background-color: black;
    }

    .target {
        background-color: red;
    }

    .pick {
        background-color: yellow;
    }

    .picked {
        background-color: yellow;
        opacity: 0.7;
        border: 1px black dashed;
    }

    .visited {
        animation-name: visitedAnimation;
        animation-duration: 1s;
        animation-timing-function: ease-out;
        animation-delay: unset;
        animation-direction: alternate;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        animation-play-state: running;
    }

    @keyframes visitedAnimation {
        0% {
            transform: scale(.3);
            background-color: rgba(0, 0, 66, 0.75);
            border-radius: 100%;
        }

        50% {
            transform: scale(1.2);
            background-color: rgba(0, 217, 159, 0.75)
        }

        100% {
            transform: scale(1.0);
            background-color: green;
        }
    }
</style>
