import Node from './node';

export default class Graph {
  constructor() {
    this.graph = {};
    this.nodes = [];
    this.picks = [];
    this.start = null;
    this.end = null;
  }

  setStart(node) {
    this.start = node;
  }

  setEnd(node) {
    this.end = node;
  }

  setPicks(nodes) {
    this.picks = nodes;
  }

  getScenarios() {
    if (!this.picks.length) return [[this.start, this.end]];
    let startLayers = this.picks.map((pick) => [this.start, pick]);
    for (let l = 1; l < this.picks.length; l += 1) {
      const newRoutes = [];
      startLayers.map((route) => {
        this.picks.map((pick) => {
          if (!route.includes(pick)) {
            newRoutes.push([...route, pick]);
          }
          return false;
        });
        return false;
      });
      startLayers = [...newRoutes];
    }
    startLayers.map((route) => {
      route.push(this.end);
      return false;
    });
    return startLayers;
  }

  static compareNode(a, b) {
    return a.position.x === b.position.x && a.position.y === b.position.y;
  }

  addNode(n) {
    this.graph[`${n.position.x}-${n.position.y}`] = n;
    this.nodes.push(n);
    // Return a reference back
    return n;
  }

  findNode(position) {
    return this.graph[`${position.x}-${position.y}`] || null;
  }

  findNodeOrCrete(position) {
    let node = this.graph[`${position.x}-${position.y}`];
    if (!node) {
      node = new Node(position);
      this.nodes.push(node);
      this.graph[`${position.x}-${position.y}`] = node;
    }
    return node;
  }
}
