export function createArray(c, r, val) {
    const m = new Array(c);
    for (let i = 0; i < c; i += 1) {
        m[i] = new Array(r);
        for (let j = 0; j < r; j += 1) {
            m[i][j] = val === undefined ? 1 : val;
        }
    }
    return m;
}

export async function nextFrame() {
    return new Promise((resolve) => {
        requestAnimationFrame(resolve);
    });
}

export function getNeighbours(sx, sy, a, maze, width, height) {
    const n = [];
    if (sx - 1 > 0 && maze[sx - 1][sy] === a && sx - 2 > 0 && maze[sx - 2][sy] === a) {
        n.push({
            x: sx - 1,
            y: sy,
        });
        n.push({
            x: sx - 2,
            y: sy,
        });
    }
    if (sx + 1 < width - 1 && maze[sx + 1][sy] === a
        && sx + 2 < width - 1 && maze[sx + 2][sy] === a) {
        n.push({
            x: sx + 1,
            y: sy,
        });
        n.push({
            x: sx + 2,
            y: sy,
        });
    }
    if (sy - 1 > 0 && maze[sx][sy - 1] === a && sy - 2 > 0 && maze[sx][sy - 2] === a) {
        n.push({
            x: sx,
            y: sy - 1,
        });
        n.push({
            x: sx,
            y: sy - 2,
        });
    }
    if (sy + 1 < height - 1 && maze[sx][sy + 1] === a
        && sy + 2 < height - 1 && maze[sx][sy + 2] === a) {
        n.push({
            x: sx,
            y: sy + 1,
        });
        n.push({
            x: sx,
            y: sy + 2,
        });
    }
    return n;
}
